<script>
import Header from "../../components/global/header/HeaderArea.vue"
import { useRouter } from "vue-router"
import { ref, onMounted } from 'vue'
import { getUrlId, getUrlId2, monthHandle3, errorHandle } from '@/utils/handle'
import { apiGetWorkOrderInfo, apiCheckWorkOrder } from "@/api/api"
import { Toast, ToastConfirm } from '@/components/global/swal'



export default {
  setup() {
    const router = useRouter()
    const careTakerId = getUrlId().urlId
    const orderUrlId = getUrlId2().urlId
    const orderUrlIdLink = `/order/info/${orderUrlId}`
    let orderData = ref([])
    let caretakerData = ref([])

    onMounted(async () => {
      try {
        orderData.value = await apiGetWorkOrderInfo({ id: orderUrlId })
        orderData.value = orderData.value.data
        caretakerData.value = orderData.value.careTakerList.filter(x => x.id == careTakerId)
        caretakerData.value = caretakerData.value[0]
        //處理日期
        if (caretakerData.value.cancelDate) {
          if (caretakerData.value.cancelDate?.length > 0) {
            let res = monthHandle3(caretakerData.value.cancelDate)
            caretakerData.value.cancelMonthInfo = res.monthInfo
            caretakerData.value.cancelDayInfo = res.dayInfo
          }
        }
        if (caretakerData.value.checkDate) {
          let res2 = monthHandle3(caretakerData.value.checkDate)
          caretakerData.value.checkMonthInfo = res2.monthInfo
          caretakerData.value.checkDayInfo = res2.dayInfo
        }
        //看護 languageInfo
        caretakerData.value.languageInfo = []
        if (caretakerData.value.language?.length > 0) {
          caretakerData.value.language.forEach(item => {
            caretakerData.value.languageInfo.push(item.name)
          })
        }
        //看護 experienceInfo
        caretakerData.value.experienceInfo = []
        if (caretakerData.value.experience?.length > 0) {
          caretakerData.value.experience.forEach(item => {
            caretakerData.value.experienceInfo.push(item.careExperience)
          })
        }
        //看護 workPlaceInfo
        caretakerData.value.workPlaceInfo = []
        if (caretakerData.value.workPlace?.length > 0) {
          caretakerData.value.workPlace.forEach(item => {
            caretakerData.value.workPlaceInfo.push(`${item.city}${item.area}`)
          })
        }
        //timePeriod工作時段
        caretakerData.value.timePeriodInfo = []
        caretakerData.value.timePeriod.forEach(item => {
          caretakerData.value.timePeriodInfo.push(item.name);
        });
        caretakerData.value.timePeriodInfo.reverse()
        if (caretakerData.value.otherJobTime != 0) {
          caretakerData.value.timePeriodInfo.push(`${caretakerData.value.otherJobTime}小時`);
        }


      } catch (error) {
        console.log(error)
      }
      // console.log(orderData.value)
      // console.log(caretakerData.value)
    })

    //確定承接建立工單
    const checkInvite = async () => {
      // console.log(caretakerData.value.applyId)
      const result = await ToastConfirm("工單成立後即無法異動，確定由此看護承接?")
      if (result == true) {
        let checkData = {
          "workOrderId": orderUrlId,
          "applyId": caretakerData.value.applyId,
          "isConfirm": 1
        }
        try {
          let msg = await apiCheckWorkOrder(checkData)
          if (msg.status == true) {
            Toast('工單已建立')
            router.push({ path: orderUrlIdLink })
          } else {
            let errorMsg = errorHandle(msg)
            Toast(errorMsg)
          }
        } catch (error) {
          console.log(error)
        }
      }
    }
    const cancelInvite = () => {
      router.push({ path: orderUrlIdLink })
    }

    return { orderData, caretakerData, orderUrlIdLink, checkInvite, cancelInvite };
  },
  components: {
    Header
  },
};
</script>

<template>
  <Header title="看護資訊" :backToPath=orderUrlIdLink />
  <div class="orderInvite">
    <div v-if="caretakerData.checkMonthInfo">
      <div class="title">照顧日期時段</div>
      <div class="main2">
        <div class="title2">接受日期</div>
        <div class="content2 accept">{{ caretakerData.checkMonthInfo[0] }}/{{
            caretakerData.checkDayInfo[caretakerData.checkMonthInfo[0]].join('、')
        }}</div>
        <div class="content2 accept" v-if="caretakerData.checkMonthInfo.length == 2">{{ caretakerData.checkMonthInfo[1]
        }}/{{
    caretakerData.checkDayInfo[caretakerData.checkMonthInfo[1]].join('、')
}}</div>
      </div>
      <div class="main2 " v-if="caretakerData.cancelMonthInfo">
        <div class="title2">取消日期</div>
        <div class="content2 cancel">{{ caretakerData.cancelMonthInfo[0] }}/{{
            caretakerData.cancelDayInfo[caretakerData.cancelMonthInfo[0]].join('、')
        }}</div>
        <div class="content2 cancel" v-if="caretakerData.cancelMonthInfo.length == 2">{{
            caretakerData.cancelMonthInfo[1]
        }}/{{
    caretakerData.cancelDayInfo[caretakerData.cancelMonthInfo[1]].join('、')
}}</div>
      </div>
      <div class="main2">
        <div class="title2">照顧時段</div>
        <div class="content2">{{ caretakerData.startTime }}~{{ caretakerData.endTime }}
          <span v-if="(caretakerData.startTime == caretakerData.endTime)">(24小時)</span>
        </div>
      </div>
    </div>
    <hr class="divider" v-if="caretakerData.checkMonthInfo">
    <div v-if="caretakerData.name">
      <div class="title">{{ caretakerData.name }}</div>
      <div class="basicInfo">
        <div class="infoText">
          <div class="main1">
            <div class="title1">性別</div>
            <div class="content1">{{ caretakerData.gender == 'Female' ? '女性' : '男性' }}</div>
          </div>
          <div class="main1">
            <div class="title1">年齡</div>
            <div class="content1">{{ caretakerData.age }}歲</div>
          </div>
          <div class="main1">
            <div class="title1">身高</div>
            <div class="content1">{{ caretakerData.height }}公分</div>
          </div>
          <div class="main1">
            <div class="title1">體重</div>
            <div class="content1">{{ caretakerData.weight }}公斤</div>
          </div>
          <div class="main1">
            <div class="title1">年資</div>
            <div class="content1">{{ caretakerData.seniority }}</div>
          </div>
          <div class="main1">
            <div class="title1">國籍</div>
            <div class="content1">{{ caretakerData.countryCode }}</div>
          </div>
          <div class="main1">
            <div class="title1">語言</div>
            <div class="content1">{{ caretakerData.languageInfo.join('、') }}</div>
          </div>
        </div>
        <img :src="caretakerData.avatarUrl" alt="" v-if="(caretakerData.avatarUrl)" onerror="this.onerror=null; this.src='https://fakeimg.pl/120x160/'">
        <img src="@/assets/image/noavatar.jpg" v-if="(!caretakerData.avatarUrl)">        
      </div>
      <div class="main2">
        <div class="title2">履歷身分</div>
        <div class="content2">{{ caretakerData.serviceRequirement.name }}</div>
      </div>
      <div class="main2">
        <div class="title2">具備證照</div>
        <div class="content2">{{ caretakerData.license.join('、') }}</div>
      </div>
      <div class="main2">
        <div class="title2">收費標準</div>
        <div class="content2" v-if="caretakerData.wage.home">居家：NT${{
            caretakerData.wage.home
        }}/小時、NT${{ caretakerData.wage.fullDay.home }}/日</div>
        <div class="content2" v-if="caretakerData.wage.home">醫院：NT${{
            caretakerData.wage.hospital
        }}/小時、NT${{ caretakerData.wage.fullDay.home }}/日
        </div>
      </div>
      <div class="main2">
        <div class="title2">照顧經驗及時數</div>
        <div class="content2">{{ caretakerData.experienceInfo.join('、') }}</div>
      </div>
      <div class="main2">
        <div class="title2">服務地區</div>
        <div class="content2">{{ caretakerData.workPlaceInfo.join('、') }}</div>
      </div>
      <div class="main2">
        <div class="title2">工作時段</div>
        <div class="content2">{{ caretakerData.timePeriodInfo.join('、') }}</div>
      </div>
      <div class="main2">
        <div class="title2">自我介紹</div>
        <div class="content2">{{ caretakerData.selfIntroduction }}</div>
      </div>
    </div>
    <hr class="divider">
    <div class="btnArea">
      <button type="button" class="btn btn-outline-primary" @click="checkInvite"
        v-if="(orderData.workOrderStatus == 2)">確定工單</button>
      <button type="button" class="btn btn-outline-primary" @click="cancelInvite">回上一頁</button>
    </div>
  </div>
</template>


<style lang="scss" scoped>
@import "@/assets/custom/config";
// @import "@/assets/custom/mixins";

.orderInvite {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
  padding-top: 22px;
  padding-left: 22px;
  padding-right: 22px;

  .basicInfo {
    display: flex;
    justify-content: space-between;

    img {
      height: 10rem;
      width: 7.5rem;
      border-radius: 4px;
    }

  }


  .title {
    font-size: 1.125rem;
    font-weight: 700;
    color: #000000;
    margin-bottom: 16px;
  }

  .main1 {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-top: 5px;
    margin-bottom: 5px;

    .title1 {
      font-size: 14px;
      font-weight: 400;
      color: #909090;
      margin-right: 14px;

    }

    .content1 {
      font-size: 16px;
      font-weight: 400;
      color: #333333;
    }
  }

  .main2 {
    margin-top: 10px;
    margin-bottom: 10px;

    .title2 {
      font-size: 14px;
      font-weight: 400;
      color: nth($secondary-color, 1);
    }

    .content2 {
      font-size: 16px;
      font-weight: 400;
      color: #333333;
    }

    .accept {
      color: #41B423;
    }

    .cancel {
      color: #FF345F;
    }

  }
}

.btnArea {
  margin: 15px 0px;
  display: flex;
  justify-content: space-evenly;
}
</style>